const RainShader = {
    vertexShader: `
    #define PI 3.14159265359
    uniform float uTime;
    uniform float uHighlight;
    uniform float uOldTime;
    uniform float uNowTime;
    uniform float uStartTime;
    uniform float uLastHighlight;
    attribute vec3 aSpeed;
    attribute vec2 aRandom;

    vec3 mod289(vec3 x) {
      return x - floor(x * (1.0 / 289.0)) * 289.0;
    }

    vec2 mod289(vec2 x) {
      return x - floor(x * (1.0 / 289.0)) * 289.0;
    }

    vec3 permute(vec3 x) {
      return mod289(((x*34.0)+1.0)*x);
    }

    float snoise(vec2 v){
      const vec4 C = vec4(0.211324865405187,  // (3.0-sqrt(3.0))/6.0
                          0.366025403784439,  // 0.5*(sqrt(3.0)-1.0)
                        -0.577350269189626,  // -1.0 + 2.0 * C.x
                          0.024390243902439); // 1.0 / 41.0
    // First corner
      vec2 i  = floor(v + dot(v, C.yy) );
      vec2 x0 = v -   i + dot(i, C.xx);

    // Other corners
      vec2 i1;
      //i1.x = step( x0.y, x0.x ); // x0.x > x0.y ? 1.0 : 0.0
      //i1.y = 1.0 - i1.x;
      i1 = (x0.x > x0.y) ? vec2(1.0, 0.0) : vec2(0.0, 1.0);
      // x0 = x0 - 0.0 + 0.0 * C.xx ;
      // x1 = x0 - i1 + 1.0 * C.xx ;
      // x2 = x0 - 1.0 + 2.0 * C.xx ;
      vec4 x12 = x0.xyxy + C.xxzz;
      x12.xy -= i1;

    // Permutations
      i = mod289(i); // Avoid truncation effects in permutation
      vec3 p = permute( permute( i.y + vec3(0.0, i1.y, 1.0 ))
        + i.x + vec3(0.0, i1.x, 1.0 ));

      vec3 m = max(0.5 - vec3(dot(x0,x0), dot(x12.xy,x12.xy), dot(x12.zw,x12.zw)), 0.0);
      m = m*m ;
      m = m*m ;
      vec3 x = 2.0 * fract(p * C.www) - 1.0;
      vec3 h = abs(x) - 0.5;
      vec3 ox = floor(x + 0.5);
      vec3 a0 = x - ox;
      m *= 1.79284291400159 - 0.85373472095314 * ( a0*a0 + h*h );

    // Compute final noise value at P
      vec3 g;
      g.x  = a0.x  * x0.x  + h.x  * x0.y;
      g.yz = a0.yz * x12.xz + h.yz * x12.yw;
      return 130.0 * dot(m, g);
    }

    vec3 getCurvePosition(float posX, float posY, float posZ){
        float noiseScale = 0.005;
        float newPosY = snoise( vec2( posX * noiseScale + ( uTime/10.) , posZ * noiseScale+ ( uTime/10. ) ) );
        vec3 newPos = vec3(0.);
        newPos.x = posX + aRandom.x *5.;
        newPos.z = posZ + sin(aRandom.y) *5.;
        newPos.y = newPosY * 30.;
        return newPos;
    }

    void main() {
        float posX = position.x;
        float posY = position.y;
        float posZ = position.y;
        vec4 mvPosition = vec4(0.);
        float timeChanged = uTime*2.;
        float timer = smoothstep( 0.0, 2.0, timeChanged);
        vec3 newPos = getCurvePosition(posX, posY, posZ);
        
        vec3 transformed = vec3(newPos.x, newPos.y+120., newPos.z-80.);
        vec3 transformedNew = transformed.xyz;
        float padding = 5.0;
        vec4 finalFinalPos = vec4(0.);
        float stepTime = (uOldTime - uStartTime)*0.001;
        float yPadding = aSpeed.z/30.;

        // aSpeed: x:index, y: rank, z:newDeathsPerWeek
        if(uHighlight == 0.){
          vec3 finalPos = mix(position, newPos, timer);
          finalFinalPos = vec4(finalPos, 1.0);
        } else if(uHighlight <= 11.){
          if(aSpeed.x < uLastHighlight){
            // old dots fall into a barchart 
            transformedNew.y = -250. + aRandom.y * yPadding; 
            transformedNew.x = -200. + aSpeed.y * padding + aRandom.x*2.;
            transformedNew.z = 0.;
          } else if(aSpeed.x >= uLastHighlight && aSpeed.x < uHighlight){
            float timer1_1 = smoothstep( 0., 3.0-aRandom.y, stepTime ); 
            float timer1_2 = smoothstep( 9.0-aRandom.y, 10.0, stepTime ); 
            vec3 endPoint = vec3( 400.* (aRandom.x-.5), -50.-200.*aRandom.y, 0.);
            if( stepTime-9.0+aRandom.y <= 0.){ 
              transformedNew = mix(transformed, endPoint, timer1_1);
            } else {
              transformedNew = mix(endPoint, vec3((-200. + aSpeed.y * padding + aRandom.x*2.), (-250. + aRandom.y * yPadding), 0.), timer1_2);
            }
          }
          finalFinalPos = vec4( transformedNew, 1.0 );
        } else if(uHighlight == 9.){ 
          vec3 temp_pos= vec3((-200. + aSpeed.y * padding + aRandom.x*2.), -280. + (aRandom.y * aSpeed.z/30.), 0.);
          vec3 fPos = mix(temp_pos, newPos, timer);
          finalFinalPos = vec4(fPos, 1.0);
        } else {          
          if(aSpeed.x < uLastHighlight){
            // old dots fall into a barchart
            transformedNew.y = -250. + aRandom.y * yPadding; 
            transformedNew.x = -200. + aSpeed.y * padding + aRandom.x*2.;
            transformedNew.z = 0.;
          } else if(aSpeed.x >= uLastHighlight && aSpeed.x <uHighlight){
            // new dots do animation
            float timer2 = smoothstep( (6.0 - mod( aSpeed.y, 12.) *.5) * aRandom.x, 9.0-aRandom.y, stepTime); 
            float timer3 = smoothstep( 9.0- aRandom.y, 10.0, stepTime ); 
            vec3 endPoint = vec3(transformed.x, -200.-mod(aSpeed.y, 12.)*100.-aRandom.y*100., transformed.z);
            if( (uOldTime - uStartTime)*0.001 -9.0+aRandom.y <= 0.){
              // the first 8-9 sec -> rain drop
              transformedNew = mix(transformed, endPoint, timer2);
            }else {
              // the later 9-10 sec -> move to bars
              transformedNew = mix(endPoint, vec3( (-200. + aSpeed.y * padding + aRandom.x*2.), (-250. + aRandom.y * yPadding), 0. ), timer3);
            }
          } 
            finalFinalPos =  vec4( transformedNew, 1.0 );
        }

        mvPosition = modelViewMatrix * finalFinalPos;
        gl_Position = projectionMatrix * mvPosition;
        // gl_PointSize =  800.0 / -mvPosition.z  * max(abs(sin(uTime*2. +posY)), 0.4) + 4. ;
        gl_PointSize =  900.0 / -mvPosition.z + 2. + (aSpeed.x <11.0? 6.: 1. );
    }
  `,
    fragmentShader: `
    uniform sampler2D pointTexture;
    uniform float uTime;

    float map(float value, float oldMin, float oldMax, float newMin, float newMax) {
        return newMin + (newMax - newMin) * (value - oldMin) / (oldMax - oldMin);
    }
    float rand(vec2 co){
      return fract(sin(dot(co.xy ,vec2(12.9898,78.233))) * 43758.5453);
    }
    void main() {
        vec3 color = vec3(1.0, 1.0, 1.0);
        vec2 toCenter = (gl_PointCoord.xy - 0.5) * 3.0;
        float len = length(toCenter);
        float a = 1.0 - len;
        gl_FragColor = vec4(color, a);

      // vec3 color = vec3(1.0,1.0,1.0);
      // gl_FragColor = vec4(color, 0.7);
      // gl_FragColor = gl_FragColor * texture2D( pointTexture, gl_PointCoord );
    }
  `
};

