import React, { useState, useEffect, useRef } from "react";
import Timeline from "./Timeline";
import Scene from "./Scene";
import data from "../data/copy.json";
import HeadContainer from "./headContainer.js";
import SoundControl from "./SoundControl";
import Step from "./Step.js";
import { animationTime } from "./data.js";
import Audio from './Audio';
import DeathTollDiv from './DeathTollDiv';
import Tooltip from './Tooltip';
import { victimData, stepTooltipArr } from "./data";
import randomNumArr from "../data/randomArr.json";

function MainCtn(props) {

    const [currentStepIndex, setCurrentStepIndex] = useState(null);
    const [startTime, setStartTime] = useState(performance.now())
    // This callback fires when a Step hits the offset threshold. It receives the
    // data prop of the step, which in this demo stores the index of the step.
    let stepData = data.content.filter(t => t.type === "step");
    let step = stepData[0].value;
    const stepCtnRef = useRef();
    const audioRef = useRef();

    const COUNT = 23350;


    const [audioEnabled, setAudioEnabled] = useState(false);

    useEffect(() => {
        showStep();
        setStartTime(performance.now());
    }, [currentStepIndex]);

    useEffect(() => {
         audioRef.current.muted = !audioEnabled;        
    }, [audioEnabled]);

    let i = 1;
    let timer;

    function showStep() {
        let stepCtn = stepCtnRef.current;
        let { children } = stepCtn;
        let ctnArr = [...children];

        ctnArr.forEach((child, i) => {
            if (i == currentStepIndex) {
                child.classList.add("active");
            } else if (child.classList.contains("active")) {
                child.classList.remove("active");
            }
        });
    }

    function onClickFn(prop) { // run the animation
        document.querySelector(".head_ctn").classList.remove("active");
        setCurrentStepIndex(0);
        console.log('play audio');
        audioRef.current.play();
        setAudioEnabled(true);
        // run the animation
        let idx = 0;
        let timer;
        loop();
        function loop(){
            setCurrentStepIndex(idx);
            timer = setTimeout(loop, animationTime[idx] * 1000);
            idx++;
            if (idx >= animationTime.length) {
                clearTimeout(timer);
            }
        }

    }

    function onSouncControlClick(){
        setAudioEnabled(!audioEnabled);
    }

    return (
        <div className="main__ctn">
            <HeadContainer
                onClickFn={onClickFn}
                currentStepIndex={currentStepIndex}
            />
            <Audio
                src={"./assets/audio/COVID_Mockup_2021_06_30.mp3"}
                audioRef={audioRef}
            />
            {stepTooltipArr[currentStepIndex] &&
                stepTooltipArr[currentStepIndex].length &&
                stepTooltipArr[currentStepIndex].map(t => {
                    return (
                        <Tooltip
                            key={`tooltip_step_${currentStepIndex}_${t}`}
                            data={victimData[t - 1]}
                            stepIndex={2}
                            pos={{ x: "52%", y: "calc(69.6% - 30px)", z: 0 }}
                            currentStepIndex={currentStepIndex}
                        />
                    );
                })}

            {/* graphic ctn */}
            <div
                id="graphic_ctn_outer"
                style={{
                    position: "sticky",
                    top: 0,
                    height: "100vh"
                }}
            >
                <SoundControl
                    step={currentStepIndex}
                    onSouncControlClick={onSouncControlClick}
                    audioEnabled={audioEnabled}
                />
                <DeathTollDiv currentStepIndex={currentStepIndex} />
                {/* <Timeline step={currentStepIndex} /> */}

                <section id="graphic_ctn">
                    <Scene
                        step={currentStepIndex}
                        randomNumArr={randomNumArr}
                        COUNT={COUNT}
                        startTime={startTime}
                    />
                </section>
                <div className="step__ctn" ref={stepCtnRef}>
                    {step.map((data, stepIndex) => (
                        <Step
                            key={`step__${stepIndex}`}
                            data={data}
                            stepIndex={stepIndex}
                            currentStepIndex={currentStepIndex}
                            setStartTime={setStartTime}
                        />
                    ))}
                </div>
            </div>
        </div>
    );
}

export default MainCtn;
