import React, { useEffect } from "react";
import {selectAll} from 'd3-selection';

function Tooltip(props) {
    let { data, stepIndex, currentStepIndex, pos } = props;
    const {name, age, descriptor, location, county } = data;
    useEffect(() => {
        selectAll(".tooltip_ctn").classed("active", true);
    }, [currentStepIndex]);
    return (
        <div
            className={`tooltip_ctn tooltip_ctn__${stepIndex}`}
            data-step={stepIndex}
            key={`tooltip_ctn__${stepIndex}`}
            style={{ position: "absolute", top: pos.y, left: pos.x }}
        >
            <p className="tooltip tooltipHeader">{name}</p>
            <p
                className="tooltip tooltipDescriptor"
                id={`tooltip__desp__${stepIndex}`}
            >
                {`${age}, ${descriptor}`}
            </p>
            <p className="tooltip tooltipLoc" id={`tooltip__loc__${stepIndex}`}>
                {`${location}, ${county}`}
            </p>
        </div>
    );
}

export default Tooltip;
