import React from "react";

function Step(props) {
    let { data, stepIndex, currentStepIndex } = props;
    
    return (
        <div
            className={`step__outer step__outer__${stepIndex}`}
            data-step={stepIndex}
            key={`step__outer__${stepIndex}`}
        >
            <div
                className={`step__inner step__inner__${stepIndex}`}
                key={`step__inner__${stepIndex}`}
            >
                <p key={`step__inner__p__${stepIndex}`}>{data["text"]}</p>
            </div>
        </div>
    );
}

export default Step;
